<template>
  <ele-upload-video
    :fileSize="20"
    :responseFn="handleResponse"
    @error="handleUploadError"
    action="http://127.0.0.1:8000/upload/video"
    name="video"
    style="margin: 50px"
    :value="video"
    @httpRequest="handleUpload"
  />
</template>

<script>
import axios from "axios";

export default {
  name: "mainpage",
  data() {
    return {
      video: null
    };
  },
  methods: {
    handleUploadError(error) {
      this.$notify.error({
        title: "上传提示",
        message: "上传文件失败"
      });
      console.log("error", error);
    },
    handleResponse(response, file) {
      this.video = "http://127.0.0.1:8000/api/getuploadvideo";
    }
  }
};
</script>

